.login-tab {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: #04171db7;
  z-index: 1000;
}

.login-tab>div {
  padding: 0 20px;
  margin: 0 auto;
}

.login-tab>div>div.content-container{
  max-width: 925px;
  padding: 3rem;
  border: 2px solid #035c77;
  border-radius: 3.5px;
  margin: 18vh auto;
  background-color: #051c24;
  box-shadow: inset 0 0 80px 40px rgb(8, 11, 24);
}

.content-container>div {
  width: 50%;
  margin-right: auto;
  margin-left: auto;
}

.form-title {
  margin-bottom: 1rem;
}

.login-tab label {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.login-tab .submit-btn {
  font-size: 1rem;
  line-height: 1rem;
  padding: .6rem 1rem;
}
.login-tab .submit-btn:hover, .login-tab .submit-btn:focus {
  background-color: rgb(255, 226, 59);
  color: #04171d;
  font-weight: 700;
}

.br-line {
  width: 100%;
  border-bottom: 4px dotted #057da1;
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.error {
  display: block;
  height: .81rem;
  margin-top: .01rem;
  margin-bottom: .51rem;
  color: aliceblue;
  font-size: .85rem;
  line-height: .85rem;
  text-align: right;
  white-space: nowrap;
}

.login-tab .create-acc-form input[name="nameq"] {
  text-transform: capitalize;
}


@media (max-width: 768px) {
  .content-container>div {
    width: 80%;
  }
}

@media (max-width: 480px) {
  .content-container>div {
    width: 100%;
  }

  .login-tab>div>div.content-container {
    padding: 2rem;
  }

  .login-tab label {
    flex-wrap: wrap;
  }

  .error {
    white-space: unset;
  }
}