.hover-zone {
  width: 2.5rem;
  height: 2.5rem;
  position: absolute;
  top: 20px;
  right: 20px;
  text-align: center;
  transition: all .3s ease;
}

.hover-zone button {
  width: 100%;
  height: 100%;
  padding: 5px;
  border: 1px solid #035c77;
  border-radius: 50px;
  font-size: 1rem;
  letter-spacing: 0;
  transition: all .3s ease;
}

.hover-zone button:hover {
  background-color: rgb(255, 226, 59);
  color: #04171d;
}