.wanted-btn, .seen-btn {
  position: absolute;
  bottom: 2px;
  transform: scale(-1, 1);
  background-color: #04171ded;
}
.seen-btn {
  right: 0;
}

.wanted-btn>button, .seen-btn>button {
  display: block;
  padding: .1rem;
  border: none;
  border-radius: 0;
  font-size: 1.4rem;
  opacity: .7;
  transition: all .3s ease;
}

.wanted-btn>button:hover, .seen-btn>button:hover {
  opacity: 1;
}

.checked-btn {
  color: #2eb0d7;
}