.App-header .first-nav .random-btn {
  height: 4.688rem;
  padding: 0 1rem;
  border-radius: 3px;
  background-color: rgb(255, 226, 59);
  color: #04171d;
  font-size: 1rem;
  line-height: 1rem;
  letter-spacing: 0.156rem;
  transition: all .6s ease;
}
.App-header .first-nav .random-btn:hover {  
  background-color: inherit;
  color: inherit;
}


@media (max-width: 1201px) {
  .App-header .first-nav .random-btn {
    height: 4.4rem;
    padding: 0 .5rem;
  }
}

@media (max-width: 768px) {
  .App-header .first-nav .random-btn {
    height: 2.37rem;
    border-radius: 50px;
  }

  .App-header .first-nav .random-btn>span {
    position: relative;
    display: inline-block;
    visibility: hidden;
    width: 100%;
  }
  .App-header .first-nav .random-btn>span::after {
    content: "...or pick a random movie";
    position: absolute;
    top: 25%;
    left: 0;
    visibility: visible;
    width: 100%;
    font-size: 1.125rem;
    letter-spacing: 0;
    word-spacing: -0.125rem;
  }
}