@font-face {
  font-family: 'Showtime Normal';
  font-style: normal;
  font-weight: normal;
  src: local('Showtime Normal'), url('/Showtime.woff') format('woff');
}

@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans&family=Poiret+One&display=swap');

button {
  background: none;
  border: 1px solid #035c77;
  border-radius: 50px;
  color: rgb(255, 226, 59);
  font-family: 'Josefin Sans', sans-serif;
  line-height: 0;
  cursor: pointer;
  transition: all .3s ease;
}

a {
  color: rgb(255, 226, 59);
}

ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  width: 100%;
  min-height: 240px;
  /* background-image: linear-gradient(#000c24 5%, #000c247e 100%); */
  background-image: linear-gradient(#000000ec 5%,  #04171dab 90%);
  font-size: 1.5rem;
  font-family: 'Poiret One', cursive;
  font-weight: bold;
  position: fixed;
  z-index: 100;
}

.App-header nav {
  width: 80%;
  width: calc(90% - 20px);
  margin: 0 auto;
}

.App-header .logo-motto, .App-header .first-nav {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.App-header .first-nav>* {
  padding: 2vw 0;
}

.App-header .motto {
  padding-left: 1.5vw;
  padding-right: 1.5vw;
  line-height: 2rem;
  font-size: 1.5rem;
}

.App-header .logo {
  height: 4.688rem;
  border: none;
  line-height: normal;
}

.App-header .logo h1 {
  font-size: 3rem;
  font-family: 'Showtime Normal', sans-serif;
  letter-spacing: -1px;
  white-space: nowrap;
}

.App-header .logo h2 {
  padding-left: 5px;
  color: #fff;
  font-weight: 600;
  line-height: .8;
  font-size: 1rem;
  letter-spacing: 0.156rem;
  white-space: nowrap;
}

.App-header .search-random {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.App-header .search-random>* {
  margin-left: .5rem;
}

.App-header .second-nav {
  margin-right: -20px;
}

.App main {
  min-height: calc(100vh - 80px);
  padding-top: 240px;
  background-color: #04171d;
}

.App footer {
  padding: 20px 0;
  background-color: #04171d;
}


@media (max-width: 1201px) {
  .App-header {
    max-height: 100vh;
    min-height: 10rem;
  }

  .App-header .motto {
    font-size: 1.3rem;
  }

  .App-header .search-random {
    padding: 0;
  }

  .App-header .second-nav {
    width: 13rem;
    height: 0rem;
    padding: .25rem;
    border-top: 4px dotted #057da1;
    border-bottom: 4px dotted #057da1;
    margin: 0 auto 1rem;
    transition: all .5s cubic-bezier(0.5, 1, 0.92, 1);
  }
  .App-header .second-nav:hover {
    height: 50rem;
    max-height: 82vh;
    border: none;
    box-sizing: content-box;
    overflow-y: scroll;
  }

  .App main {
    padding-top: 10rem;
  }

}

@media (max-width: 1024px) {
  .App-header .motto {
    width: 12rem;
  }
}

@media (max-width: 768px) {
  .App-header .first-nav>* {
    padding: 1.5rem 0 0 0;
  }

  .App-header .search-random {
    flex-direction: column;
    align-items: stretch;
  }

  .App-header .search-random>* {
    margin: 0 0 .5rem 0;
  }

  .App-header .search-random>span:nth-child(2) {
    display: none;
  }

  .App-header .second-nav {
    margin-top: 1rem;
  }
}

@media (max-width: 480px) {
  .App main {
    padding-top: 15rem;
  }

  .App-header .second-nav:hover {
    max-height: 73vh;
  }

  .App-header .motto {
    width: 9.5rem;
  }
}