.login-btn {
  position: fixed;
  left: 2.5%;
  bottom: 5%;
  padding: 1rem;
  background-image: linear-gradient(#04171d91 10%, #031319 90%);
  font-size: 2rem;
  text-align: center;
}

.login-btn:hover {
  background-color: rgb(255, 226, 59);
  color: #04171d;
}

@media (max-width: 480px) {
  .login-btn {
    padding: .8rem;
    font-size: 1.6rem;
  }
}