.movie-card {
  position: relative;
  border: 1px solid #035c77;
  border-radius: 3px;
  margin: 10px;
  background-color: #04171d;
  cursor: pointer;
  transition: all .5s cubic-bezier(0.5, 1, 0.92, 1);
}
.movie-card:hover {
  color: #04171d;
  background-color: rgb(255, 226, 59); 
}

.movie-card>div:first-child {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 13.125rem;
  height: 22.5rem;
  overflow: hidden;
}

.movie-card:hover img {
  transform: scale(1.04);
}
.movie-card:hover h2 {
  font-weight: 600;
}

.movie-card .img-container {
  height: 80%;
  background-color: #04171d;
}

.movie-card img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transform: scale(1);
  transition: transform .35s cubic-bezier(0.9, 1, 0.92, 1);
}

.movie-card .text-container {
  height: 20%;
  padding: 0.65rem 0.25rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.movie-card h2 {
  max-height: 1.85rem;
  margin-bottom: 0.6rem;
  margin: auto;
  font-size: 0.95rem;
  font-weight: 400;
  letter-spacing: .015rem;
  word-spacing: -.15rem;
  overflow: hidden;
}

.movie-card h3 {
  padding: 0 0.3rem;
  margin: 0;
  font-size: 0.8rem;
  font-weight: 200;
}


@media (max-width: 1024px) {
  .movie-card {
    margin: 5px;
  } 
}

@media (max-width: 1024px) {
  .movie-card {
    margin: 2.5px;
  }
}