.info-tab {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: #04171db7;
  z-index: 1000;
}

.info-tab>div {
  width: 80%;
  margin: 0 auto;
}

.info-tab>div>div:not(.hover-zone){
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  max-width: 925px;
  overflow-y: auto;
  max-height: 80vh;
  padding: 3rem;
  border: 2px solid #035c77;
  border-radius: 3.5px;
  margin: 10vh auto;
  background-color: #051c24;
  box-shadow: inset 0 0 80px 40px rgb(8, 11, 24);
  text-align: left;
  position: relative;
}

.info-tab .text-cont {
  flex: 66.6%;
  width: 100%;
  max-width: 70vw;
  padding: 2rem 3rem 2rem 0;
}

.info-tab .genres-val {
  margin-bottom: .7rem;
  text-transform: lowercase;
  font-family: 'Poiret One', cursive;
  font-size: 1.25rem;
  color: rgb(255, 255, 255);
}

.info-tab h3 {
  margin-top: .3rem;
  margin-bottom: 1.4rem;
  font-family: 'Poiret One', cursive;
  font-size: 1rem;
}

.info-tab .vote-val {
  font-size: 1.6rem;
  color: rgb(255, 255, 255);
}

.info-tab .lang-val {
  display: inline-block;
  min-width: 37px;
  padding: 5px 5px 3px;
  border: 1px solid #035c77;
  border-radius: 3.5px;
  margin-right: .7rem;
  margin-bottom: 2.1rem;
  text-align: center;
  text-transform: lowercase;
  font-size: 1.1rem;
  line-height: 1.875rem;
  color: #fff;
}

.info-tab .movie-overview {
  text-align: justify;
}

.btns-container {
  position: relative;
  width: 5.75rem;
  height: 38px;
  margin-top: 2rem;
}

.btns-container>*:first-child, .btns-container>*:nth-child(2) {
  padding: 5px;
  border: 1px solid #035c77;
  border-radius: 3.5px;
}

.info-tab .img-cont {
  flex: 33.4%;
  min-width: 230px;
  align-self: center;
  text-align: center;
}

.info-tab .img-cont img {
  width: 100%;
  max-width: 33vw;
  object-fit: cover;
}

.img-cont button, .del-acc-tab .del-acc-btn {
  display: block;
  height: fit-content;
  padding: 1.25rem;
  margin: 10px auto;
  line-height: normal;
}

.hov-btn:hover {
  background-color: rgb(255, 226, 59);
  color: #04171d;
  font-weight: 700;
}

.profile-cont p {
  padding: 40px 0 8px 0;
}

input[type='checkbox'] {
  margin: 0 12px;
  cursor: pointer;
}

.info-tab .del-acc-tab {
  position: fixed;
  top: calc(50% - 150px);
  left: calc(50% - 12.5rem);
}
.info-tab .del-acc-tab>div {
  margin: 0 !important;
  width: 25rem;
  height: 300px !important;
}


@media (max-width: 768px) {
  .info-tab .text-cont {
    padding-right: 0;
  }

  .info-tab .img-cont img {
    max-width: 45vw;
  }
}

@media (max-width: 480px) {
  .info-tab>div {
    width: 90%;
  }

  .info-tab>div>div:not(.hover-zone) {
    padding: 2rem;
  }

  .info-tab .img-cont {
    flex: 100%;
    min-width: unset;
  }

  .info-tab .img-cont img {
    max-width: unset;
  }
}