.search-input {
  height: 2.75rem;
  min-width: 19.375rem;
  padding: .45rem;
  border: 1.5px solid #057da1;
  border-radius: 30px;
  outline: none;
  text-indent: 2.25%;
  font-family: 'Josefin Sans', sans-serif;
  font-size: 1.125rem;
  font-weight: 500;
  letter-spacing: -.031rem;
  word-spacing: -.125rem;
  background-color: unset;
  color: #fff;
}

@media (max-width: 768px) {
  .search-input {
    min-width: 16rem;
    height: 2.37rem;
    text-align: center;
  }
}