.list-of-genres {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  margin-bottom: 2rem;
}

.list-of-genres .btn-container {
  width: 13.125rem;
  height: .9em;
  border-bottom: 4px dotted #057da1;
  margin-right: 20px;
  margin-bottom: 2px;
  overflow: hidden;
  transition: all .5s cubic-bezier(0.5, 1, 0.92, 1);
}
.list-of-genres:hover .btn-container {
  height: 1.2em;
}

.list-of-genres .reset-btn {
  width: 13.125rem;
  padding: 0;
  border: none;
  margin-right: 20px;
  color: rgb(255, 255, 255);
  font-size: 1.2rem;
  font-weight: 300;
  line-height: 1.25rem; 
  letter-spacing: 1px;
  text-align: left;
  transition: all .3s ease;
}
.list-of-genres .genres-btn { 
  min-width: fit-content;
  width: 100%;
  padding: 0;
  border: none;
  color: rgb(255, 255, 255);
  font-family: 'Poiret One';
  font-size: .75em;
  font-weight: 600;
  text-align: left;
  text-transform: lowercase;
  letter-spacing: .18vw;
  word-spacing: -2px;
  opacity: 0;
  transition: all .3s ease;
}
.list-of-genres:hover .genres-btn {
  opacity: 1;
}
.list-of-genres .genres-btn:hover, .list-of-genres .reset-btn:hover {
  color: rgb(255, 226, 59);
}


@media (max-width: 1201px) {
  .list-of-genres {
    display: none;
    transition: all .8s cubic-bezier(0.5, 1, 0.92, 1);
  }
  
  .App-header .second-nav:hover .list-of-genres {
    display: flex;
    width: 11rem;
    padding: 10% 0 10% 1.25rem;
    border-left: 4px dotted #057da1;
    border-right: 4px dotted #057da1;
    margin: 0 auto;
  }

  .App-header .second-nav:hover .btn-container {
    height: fit-content;
    min-height: 2rem;
    border: none;
    margin-right: 1.25rem;
  }

  .App-header .second-nav:hover .genres-btn {
    line-height: normal;
    opacity: 1;
  }

  .list-of-genres .reset-btn {
    width: fit-content;
  }
}