#acc-bar {
  position: fixed;
  left: -100vw;
  bottom: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top-right-radius: 3px;
  background-image: linear-gradient(#04171d91 10%, #031319 90%);
  /* transition: all 2s ease; */
  transition: all .75s cubic-bezier(0.5, 1, 0.92, 1);
  /* pointer-events: none; */
}

#acc-bar>button {
  display: block;
  width: min-content;
  border: none;
  margin: 0 .8rem;
  line-height: normal;
  /* pointer-events: auto; */
}
#acc-bar .close-profile-bar {
  width: 5vw;
  margin: 0;
  font-size: 1.5rem;
}

#acc-bar>button>div:first-child {
  display: flex;
  justify-content: center;
  font-size: 1.5rem;
}

#acc-bar>button>div:first-child>span {
  width: 4rem;
  height: 4rem;
  padding: 1rem; 
  border: 1px solid #035c77;
  border-radius: 50px;
  font-size: 2rem;
  transition: all .75s cubic-bezier(0.5, 1, 0.92, 1);
}
#acc-bar>button>div:first-child>span:hover {
  background-color: rgb(255, 226, 59);
  color: #04171d;
}

#acc-bar>button>div:nth-child(2) {
  margin: 0.5rem 0 0.8rem 0;
}

#acc-bar.opened {
  left: 0vw;
}

.acc-lists {
  display: flex;
  justify-content: space-between;
  width: 95vw;
}

.edit-profile {
  border-color: transparent;
  transition: all .3s cubic-bezier(0.5, 1, 0.92, 1);
}
.edit-profile:hover {
  border-color: inherit;
}

.acc-lists>* {
  padding: .5rem;
  margin: 2rem;
  line-height: normal;
}

.acc-lists svg {
  transform: scale(-1, 1);
}


@media (max-width: 1024px) {
  #acc-bar button {
    font-size: .9rem;
  }

  .acc-lists>* {
    margin: 0 1rem;
  }
}